import React from 'react';
import { default as MuiInput, InputProps } from '@mui/material/Input';

const Input = React.forwardRef<HTMLElement, InputProps>( ( props, ref ) => {
	return (
		<MuiInput
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<InputProps>;

export default Input;
